html,
body {
  background-color: #f8f8f8;
  font-family: Roboto;
}

.suggestion {
  margin-left: 10px;
}

.pagination {
  display: inline-block;
}

.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
}

.pagination a.active {
  background-color: #1a5d99;
  color: white;
  pointer-events: none;
}

.pagination a:hover:not(.active) {
  background-color: #ddd;
}

.disable {
  color: #000;
  opacity: 0.8;
  pointer-events: none;
}
